import React, { useEffect, useLayoutEffect, useState } from "react";
import {
  Avatar,
  Box,
  Button,
  Dialog,
  DialogActions,
  DialogTitle,
  Grid,
  IconButton,
  Snackbar,
  TextField,
  Typography,
} from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Field, Form, Formik, useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import errorIcon from "./../../../assets/images/state/error-icon.svg";
import lock from "./../../../assets/images/profile/lock.webp";
import avatarGallery from "./../../../assets/images/profile/avatarGallery.svg";
import cookie from "cookie_js";
import {
  editProfile,
  getProfileData,
  postChangePassword,
  postProfilePhoto,
  removeImgAvatar,
  setMyProfile,
} from "../../../redux/reducers/myProfile";
import { Alert } from "@mui/material";
import Reload from "./../../../components/Reload";
import DeleteIcon from "@mui/icons-material/Delete";
import { useHistory } from "react-router";
import { MaskCustom } from "../../../components/inputMask/unicalMask";
import { useLocation } from "react-router";
import { Link } from "react-router-dom";

const Profile = ({ t }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const useStyles = makeStyles((theme) => ({
    ProfileBlock: {
      padding: 24,
      backgroundColor: "#fff",
      borderRadius: 8,
      height: "100%",
      [theme.breakpoints.down("lg")]: {
        padding: 16,
      },
    },
    ProfileBlockImg: {
      width: 100,
      height: 100,
      borderRadius: 50,
      textAlign: "center",
      margin: "0 auto",
      // marginBottom: 16,
      cursor: "pointer",
      display: "block",
      position: "relative",
      "&::before": {
        content: `"" !important`,
        position: "absolute",
        width: 50,
        height: 50,
        right: "50%",
        top: "50%",
        backgroundSize: "cover",
        transform: "translate(50%, -50%)",
        backgroundImage: `url(${avatarGallery})`,
        zIndex: 1,
      },
      "&::after": {
        content: `"" !important`,
        position: "absolute",
        width: "100%",
        height: "100%",
        right: 0,
        top: 0,
        backgroundColor: `rgba(0,0,0, .4)`,
      },
    },
    ProfileInput: {
      "& input": {
        color: "#64748B",
      },
      "& .MuiOutlinedInput-root": {
        backgroundColor: "#F7F8F9",
      },
    },
    ProfileButton: {
      backgroundColor: "#FCE34D",
      padding: "17px 0",
      width: "100%",
      marginTop: 32,
    },
    ProfileWarning: {
      marginTop: 20,
    },
    warningText: {
      color: "#F59E0B",
      marginLeft: 10,
    },
    Dialog: {
      backgroundColor: "#F7F8F9",
      padding: "30px",
    },
    // DialogTitle: {
    //   fontSize: 48,
    //   fontWeight: 600,
    // },
    AvatarIcon: {
      position: "relative",
    },
    ButtonDelete: {
      position: "absolute",
      padding: "4px!important",
      minWidth: "unset",
      right: "-5px",
      top: "-10px",
      zIndex: 1,
    },
  }));
  const classes = useStyles();
  const domain = window.location.hostname;
  const location = useLocation();
  const [loading, setLoading] = useState(false);
  const { profileData, answerMyProfileAlert } = useSelector(
    ({ MyProfileReducer }) => MyProfileReducer
  );
  // eslint-disable-next-line
  const [file, setFile] = useState(null);
  const profileDataList = profileData?.data && profileData?.data.profile;

  const handleChange = function loadFile(event) {
    if (event.target.files.length > 0) {
      const file = URL.createObjectURL(event.target.files[0]);
      setFile(file);
      var data = new FormData();
      data.append("photo", event.target.files[0]);
      dispatch(postProfilePhoto(data));
      setLoading(true);
    }
  };

  useLayoutEffect(() => {
    if (answerMyProfileAlert.severity) {
      setLoading(false);
      dispatch(getProfileData());
    }
    // eslint-disable-next-line
  }, [answerMyProfileAlert.severity]);

  const logout = () => {
    cookie.removeSpecific("token_netex", {
      path: "/",
      domain: domain,
    });
    history.push("/");
    setTimeout(() => {
      window.location.reload();
    }, 200);
  };

  const [open, setOpen] = React.useState({
    logout: false,
    removeAvatar: false,
  });

  const handleClickOpen = (key, value) => {
    setOpen((open) => ({ ...open, [key]: !open[value] }));
  };

  const handleClose = (key, value) => {
    setOpen((open) => ({ ...open, [key]: !open[value] }));
  };

  const removeAvatar = () => {
    setLoading(true);
    handleClose("removeAvatar", "removeAvatar");
    dispatch(removeImgAvatar());
  };

  const formik = useFormik({
    initialValues: {
      firstname: "",
      lastname: "",
      phone: "",
    },
    onSubmit: (values) => {
      values.phone = values.phone.replace(/[\s+().,-]/g, "");

      dispatch(editProfile(values));

      location.search && (window.location.href = location.search.split("=")[1]);
    },
  });

  useEffect(() => {
    formik.setFieldValue("firstname", profileData?.data?.profile?.firstname);
    formik.setFieldValue("lastname", profileData?.data?.profile?.lastname);
    profileData?.data?.profile?.phone &&
      formik.setFieldValue("phone", `+9${profileData?.data?.profile?.phone}`);
    // eslint-disable-next-line
  }, [profileData]);

  return (
    <>
      {loading && <Reload />}
      <Snackbar
        open={answerMyProfileAlert.open}
        autoHideDuration={2000}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        onClose={() => dispatch(setMyProfile({ ...alert, open: false }))}
      >
        <Alert
          onClose={() => dispatch(setMyProfile({ ...alert, open: false }))}
          severity={answerMyProfileAlert.severity === 200 ? "success" : "error"}
          variant="filled"
        >
          {answerMyProfileAlert.severity === 200
            ? `${t("successfullyChanged")}`
            : answerMyProfileAlert.message &&
              answerMyProfileAlert.message.messages}
        </Alert>
      </Snackbar>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={12} md={12} lg={6} xl={6}>
          <Box className={classes.ProfileBlock}>
            <Dialog
              open={open.removeAvatar}
              onClose={handleClose}
              aria-labelledby="alert-dialog-title"
              aria-describedby="alert-dialog-description"
            >
              <Box className={classes.Dialog}>
                <DialogTitle
                  id="alert-dialog-title"
                  className={classes.DialogTitle}
                >
                  {t("confirmToDeleteMessage")}
                </DialogTitle>
                <DialogActions className={classes.DialogActions}>
                  <Button
                    variant="contained"
                    size="large"
                    color="primary"
                    onClick={() => handleClose("removeAvatar", "removeAvatar")}
                  >
                    {t("cancel")}
                  </Button>
                  <Button
                    variant="contained"
                    size="large"
                    color="primary"
                    style={{ textTransform: "initial" }}
                    onClick={() => removeAvatar()}
                  >
                    {t("yesDelete")}
                  </Button>
                </DialogActions>
              </Box>
            </Dialog>

            <Box style={{ display: "flex", justifyContent: "center" }}>
              <input
                type="file"
                onChange={handleChange}
                id="upload"
                accept="image/*"
                style={{ display: "none" }}
              />
              <label htmlFor="upload">
                <IconButton
                  className={classes.AvatarIcon}
                  style={{ padding: 0 }}
                  color="primary"
                  aria-label="upload picture"
                  component="span"
                >
                  <Button
                    className={classes.ButtonDelete}
                    onClick={() =>
                      handleClickOpen("removeAvatar", "removeAvatar")
                    }
                  >
                    <DeleteIcon />
                  </Button>
                  <Avatar
                    id="avatar"
                    className={classes.ProfileBlockImg}
                    src={profileDataList?.avatar}
                  />
                </IconButton>
              </label>
              <label htmlFor="avatar" />
            </Box>

            <form onSubmit={formik.handleSubmit}>
              <span className="label" style={{ color: "#323A46" }}>
                {t("Name")}
              </span>
              <TextField
                fullWidth
                label={t("Name")}
                type="text"
                variant="outlined"
                name="firstname"
                value={formik.values.firstname}
                onChange={formik.handleChange}
                disabled
                className={classes.ProfileInput}
              />
              <span className="label" style={{ color: "#323A46" }}>
                {t("LastName")}
              </span>
              <TextField
                fullWidth
                label={t("LastName")}
                type="text"
                disabled
                variant="outlined"
                name="lastname"
                value={formik.values.lastname}
                onChange={formik.handleChange}
                className={classes.ProfileInput}
              />
              <span className="label" style={{ color: "#323A46" }}>
                {t("PhoneNumber")}
              </span>
              <TextField
                fullWidth
                label={
                  !profileData?.profile?.phone
                    ? t("phoneNumber")
                    : `+9${profileData?.profile?.phone}`
                }
                type="text"
                disabled={
                  true
                  // profileData?.data?.profile?.phone
                }
                variant="outlined"
                name="phone"
                // id="+996 (000) 00 00 00"
                InputProps={{
                  inputComponent: MaskCustom,
                }}
                value={formik.values.phone}
                onChange={formik.handleChange}
                className={classes.ProfileInput}
              />
              {!profileData?.data?.profile?.phone && (
                <Box
                  style={{ marginTop: 10 }}
                  display="flex"
                  alignItems="start"
                >
                  <img className="icon-md" src={errorIcon} alt="errorIcon" />{" "}
                  <Typography color="error" style={{ marginLeft: 10 }}>
                    {t("NotSpecified")}. {t("toLinkPhoneNumber")}{" "}
                    <Link
                      style={{ color: "#089de3" }}
                      to="/dashboard/settings/verification"
                    >
                      {t("VerificationAccountt")}
                    </Link>
                  </Typography>
                </Box>
              )}
              <span className="label" style={{ color: "#323A46" }}>
                {t("email")}
              </span>

              <TextField
                fullWidth
                label={profileData?.data?.profile?.email}
                type="email"
                variant="outlined"
                name="email"
                onChange={formik.handleChange}
                className={classes.ProfileInput}
                disabled
              />

              {/* <Button type="submit" className={classes.ProfileButton}>
                {t("Save")}
              </Button> */}
            </form>
          </Box>
        </Grid>
        <Grid item xs={12} sm={12} md={12} lg={6} xl={6}>
          <Box className={classes.ProfileBlock}>
            <img className={classes.ProfileBlockImg} src={lock} alt="lock" />
            <Formik
              initialValues={{
                old_password: "",
                new_password: "",
                confirm_password: "",
              }}
              onSubmit={(values, { resetForm }) => {
                setLoading(true);
                dispatch(postChangePassword(values));
                resetForm();
              }}
              render={(formik) => (
                <Form>
                  <span className="label" style={{ color: "#323A46" }}>
                    {t("OldPassword")}
                  </span>
                  <Field
                    render={({ field, form }) => (
                      <TextField
                        fullWidth
                        label={t("LabelOldPassword")}
                        type="password"
                        variant="outlined"
                        name="old_password"
                        value={formik.values.old_password}
                        onChange={formik.handleChange}
                        className={classes.ProfileInput}
                        required
                      />
                    )}
                  />
                  <span className="label" style={{ color: "#323A46" }}>
                    {t("NewPassword")}
                  </span>
                  <Field
                    // eslint-disable-next-line
                    render={({}) => (
                      <TextField
                        fullWidth
                        label={t("LabelNewPassword")}
                        type="password"
                        variant="outlined"
                        name="new_password"
                        value={formik.values.new_password}
                        onChange={formik.handleChange}
                        style={{ marginBottom: 8 }}
                        className={classes.ProfileInput}
                        required
                      />
                    )}
                  />
                  <Field
                    // eslint-disable-next-line
                    render={({}) => (
                      <TextField
                        fullWidth
                        label={t("LabelRepeatNewPassword")}
                        type="password"
                        variant="outlined"
                        name="confirm_password"
                        value={formik.values.confirm_password}
                        onChange={formik.handleChange}
                        className={classes.ProfileInput}
                        required
                      />
                    )}
                  />
                  <Button
                    type="submit"
                    variant="contained"
                    color="primary"
                    size="large"
                    fullWidth
                    sx={{ mt: 2 }}
                    // className={classes.ProfileButton}
                    disabled={loading}
                  >
                    {t("Save")}
                  </Button>
                </Form>
              )}
            />
          </Box>
        </Grid>
        <Dialog
          open={open.logout}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <Box className={classes.Dialog}>
            <DialogTitle
              id="alert-dialog-title"
              className={classes.DialogTitle}
            >
              {t("logoutTitle")}
            </DialogTitle>
            <DialogActions className={classes.DialogActions}>
              <Button
                variant="contained"
                size="large"
                color="primary"
                onClick={() => handleClose("logout", "logout")}
              >
                {t("Noo")}
              </Button>
              <Button
                variant="contained"
                size="large"
                color="primary"
                onClick={() => logout()}
              >
                {t("Yees")}
              </Button>
            </DialogActions>
          </Box>
        </Dialog>
      </Grid>

      <Button
        variant="contained"
        color="error"
        fullWidth
        sx={{ mt: 2 }}
        size="large"
        onClick={() => handleClickOpen("logout", "logout")}
      >
        {t("LogOut")}
      </Button>
    </>
  );
};

export default Profile;
